@import url('https://fonts.googleapis.com/css?family=Audiowide|Germania+One|Poiret+One');
.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #cfffff;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.content-container {
    width: 100%;
    background-color: #fefefe;
}
.language {
    font-style: italic;
}
.file-name {
    font-style: italic;
}
.content {
    width: 100%;
    margin: 40px auto;
    text-align: left;
    background-color: #eee;
    padding: 30px;
}
.sub-content {
    margin: 0 auto;
    width: 95%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.language-sub-content {
    margin: 0 auto;
    width: 95%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
pre {
    background-color: #ddd;
    margin-left: 15px;
    margin-right: 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 30px;
}
.language-subcontent-header,
.file-name-header {
    font-family: 'Poiret One', cursive;
    font-size: 1.2em;
}
.subcontent-header {
    font-family: 'Poiret One', cursive;
    font-size: 1.6em;
}
.content-header {
    font-family: 'Audiowide', cursive;
    font-style: bold;
    font-size: 2em;
}



